import React from 'react';
import BotaoVoltar from '../BotaoVoltar';
import './style.css';

const Scrum = () => (
    <div>
        <h1>SCRUM</h1>
        <p>Scrum é uma metodologia usada para a gestão dinâmica de projetos, sendo muitas vezes aplicada para o desenvolvimento ágil de um software. O scrum é uma ferramenta que permite controlar de forma eficaz e eficiente o trabalho, potencializando as equipes que trabalham em prol de um objetivo em comum.</p>

        <img className="logo_scrum" src="assets/scrum_logo.png" alt="logo scrum"/>

        <a className="scrum" href="https://www.scrum.org/">Site oficial do SCRUM</a>


        <BotaoVoltar/>
    </div>
);

export default Scrum;