import React from 'react';
import BotaoVoltar from '../BotaoVoltar';
import './style.css';

const SEO = () => (
    <div>
        <h1>SEO - Google</h1>
        <p>SEO significa Search Engine Optimization (otimização para mecanismos de busca). É um conjunto de técnicas de otimização para sites, blogs e páginas na web. Essas otimizações visam alcançar bons rankings orgânicos gerando tráfego e autoridade para um site ou blog.</p>

        <img className="logo_seo" src="assets/logo_seo.jpg" alt="logo SEO Google"/>
        
        <a className="google" href="https://www.google.com/">Site oficial do Google</a>

        <BotaoVoltar/>
    </div>
);

export default SEO;