import React from 'react';
import BotaoVoltar from '../BotaoVoltar';
import './style.css';

const PaginaBackEnd = () => (
    <div>
        <h1>NodeJS</h1>
        <p>Node.js é uma tecnologia usada para executar o código javascript fora do navegador. Com ele podemos construir aplicações em web em geral, desde web sites até APIs e microserviços.</p>

        <img className="logo_node" src="assets/logo-node.png" alt="logo Node"/>
        
        <a className="Node" href="https://nodejs.org/pt-br/">Site oficial do NodeJS</a>

        <BotaoVoltar/>
    </div>
);

export default PaginaBackEnd;