import React from 'react';
import BotaoVoltar from '../BotaoVoltar';
import './style.css';

const BancoDeDados = () => (
    <div>
        <h1>My SQL</h1>
        <p>O MySQL é um sistema gerenciador de banco de dados relacional de código aberto usado na maioria das aplicações gratuitas para gerir suas bases de dados</p>
        
        <img className="logo_mysql" src="assets/mysql_logo.png" alt="logo MySql"/>
        
        <a className="sql" href="https://www.mysql.com/">Site oficial do My SQL</a>

        <BotaoVoltar/>
    </div>
);

export default BancoDeDados;