import React from 'react';
import { Link } from 'react-router-dom';
import './style.css';

const PaginaInicial = () => (
    <div className="pg_inicial"> 
    <h1>Escolha seu caminho</h1>

    <div className="conteudo-opcoes">
        <Link className="opcao-front-end" to='/front-end'>Front-end</Link>
        <Link className="opcao-back-end" to='/back-end'>Back-end</Link>
        <Link className="opcao-banco-de-dados" to='/banco-de-dados'>Banco de Dados</Link>
        <Link className="opcao-scrum" to='/scrum'>SCRUM</Link>
        <Link className="opcao-seo" to='/seo'>SEO - Google</Link>
    </div>
    </div>

    
);

export default PaginaInicial;